.previews-grid{padding-top:20px;padding-bottom:20px;position:relative}@media (max-width:767px){.previews-grid{padding-top:0}}.previews-grid--1-row{min-height:444px}@media (max-width:1320px){.previews-grid--1-row{min-height:395px}}.previews-grid--2-row{min-height:888px}@media (max-width:1320px){.previews-grid--2-row{min-height:395px}}.previews-grid__slide{width:100%;display:grid;grid-template-columns:repeat(7,1fr);grid-gap:14px}@media (max-width:1320px){.previews-grid__slide{display:block}}.previews-grid__item{padding:1px;display:none}.previews-grid__slide .previews-grid__item{display:block}.previews-grid__item--big,.previews-grid__item--middle{height:444px}.previews-grid__item--small{height:215px}.previews-grid__nav{position:static}.previews-grid__nav-button{width:50px;height:50px;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:horizontal;-webkit-box-direction:normal;-ms-flex-direction:row;flex-direction:row;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-align:center;-ms-flex-align:center;align-items:center;position:absolute;top:calc(50% - 25px);cursor:pointer;z-index:2}.previews-grid__nav-button[aria-disabled=true]{opacity:.1}.previews-grid__nav-button--prev{left:-55px;-webkit-transform:scaleX(-1);-ms-transform:scaleX(-1);transform:scaleX(-1)}@media (max-width:767px){.previews-grid__nav-button--prev{left:-10px;border:1px solid var(--light-gray-color);border-top-left-radius:30px;border-bottom-left-radius:30px;border-right:none;-webkit-box-shadow:0 0 10px 4px rgba(0,0,0,.05);box-shadow:0 0 10px 4px rgba(0,0,0,.05)}}.previews-grid__nav-button--next{right:-55px}@media (max-width:767px){.previews-grid__nav-button--next{right:-10px;border:1px solid var(--light-gray-color);border-top-left-radius:30px;border-bottom-left-radius:30px;border-right:none;-webkit-box-shadow:0 0 10px 4px rgba(0,0,0,.05);box-shadow:0 0 10px 4px rgba(0,0,0,.05)}}@media (max-width:767px){.previews-grid__nav-button{background:var(--white-color)