.partners{position:relative}.partners__nav{position:absolute;width:100%;top:0;left:0}.partners__nav-button{width:50px;height:50px;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:horizontal;-webkit-box-direction:normal;-ms-flex-direction:row;flex-direction:row;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-align:center;-ms-flex-align:center;align-items:center;position:absolute;top:20px;cursor:pointer}.partners__nav-button--prev{left:-55px;-webkit-transform:scaleX(-1);-ms-transform:scaleX(-1);transform:scaleX(-1)}.partners__nav-button--next{right:-55px}.partners__nav-button-icon{width:18px;height:18px;fill:var(--black-color)}.partners__nav-button:hover .partners__nav-button-icon{fill:var(--red-color)}.partners__slider-item{height:90px;width:100%;border:1px solid var(--light-gray-color);border-radius:6px;padding:30px}@media (max-width:1320px){.partners__slider-item{height:50px;padding:15px}}.partners__slider-item-img{width:100%;height:100%;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:horizontal;-webkit-box-direction:normal;-ms-flex-direction:row;flex-direction:row;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-align:center;-ms-flex-align:center;align-items:center;font-size:50px}.partners__slider-item:hover .partners__slider-item-img{opacity:.7}