.breadcrumb{padding:30px 0;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:horizontal;-webkit-box-direction:normal;-ms-flex-direction:row;flex-direction:row;-webkit-box-pack:start;-ms-flex-pack:start;justify-content:flex-start;-webkit-box-align:start;-ms-flex-align:start;align-items:flex-start;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-ms-flex-wrap:wrap;flex-wrap:wrap}.breadcrumb__item{color:var(--black-color)}.breadcrumb__item:hover{color:var(--blue-color)}.breadcrumb__item--active,.breadcrumb__item--active:hover{color:var(--gray-color)}.breadcrumb__separator{width:8px;height:8px;display:block;margin:0 5px;fill:var(--gray-color)}