.main-banner{position:relative;height:475px;border-radius:5px;overflow:hidden;margin-bottom:12px}@media (max-width:1320px){.main-banner{height:200px}}.main-banner__img{width:100%;height:100%;-o-object-fit:cover;object-fit:cover;-o-object-position:center;object-position:center;-webkit-touch-callout:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.main-banner__search{position:absolute;right:47px;top:212px;width:536px}@media (max-width:1320px){.main-banner__search{right:auto;left:10px;top:30px;width:calc(100% - 20px)}}.main-banner__search-icon{position:absolute;left:20px;top:15px;fill:var(--red-color);width:32px;height:32px;z-index:1}@media (max-width:1320px){.main-banner__search-icon{left:13px;top:12px;width:18px;height:18px}}