.lead{padding:10px 130px}@media (max-width:1023px){.lead{padding:10px 0}}.lead__form{display:grid;grid-template-columns:repeat(3,1fr);grid-gap:20px}@media (max-width:767px){.lead__form{grid-template-columns:1fr;grid-template-rows:repeat(3,50px);grid-gap:10px}}.lead__form-button{width:100%;height:100%;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:horizontal;-webkit-box-direction:normal;-ms-flex-direction:row;flex-direction:row;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-align:center;-ms-flex-align:center;align-items:center;background:var(--dark-gray-color);color:var(--white-color);font-size:18px;border-radius:8px;cursor:pointer;border:1px solid var(--dark-gray-color)}.lead__form-button:hover{color:var(--black-color);border-color:var(--black-color);background:var(--white-color)}