.previews-slider{position:relative}.previews-slider__nav{position:static}.previews-slider__nav-button{width:50px;height:50px;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:horizontal;-webkit-box-direction:normal;-ms-flex-direction:row;flex-direction:row;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-align:center;-ms-flex-align:center;align-items:center;position:absolute;top:calc(50% - 25px);cursor:pointer;z-index:2}.previews-slider__nav-button[aria-disabled=true]{opacity:.1}.previews-slider__nav-button--prev{left:-55px;-webkit-transform:scaleX(-1);-ms-transform:scaleX(-1);transform:scaleX(-1)}@media (max-width:767px){.previews-slider__nav-button--prev{left:-10px;border:1px solid var(--light-gray-color);border-top-left-radius:30px;border-bottom-left-radius:30px;border-right:none;-webkit-box-shadow:0 0 10px 4px rgba(0,0,0,.05);box-shadow:0 0 10px 4px rgba(0,0,0,.05)}}.previews-slider__nav-button--next{right:-55px}@media (max-width:767px){.previews-slider__nav-button--next{right:-10px;border:1px solid var(--light-gray-color);border-top-left-radius:30px;border-bottom-left-radius:30px;border-right:none;-webkit-box-shadow:0 0 10px 4px rgba(0,0,0,.05);box-shadow:0 0 10px 4px rgba(0,0,0,.05)}}@media (max-width:767px){.previews-slider__nav-button{background:var(--white-color)}}.previews-slider__nav-button-icon{width:18px;height:18px;fill:var(--black-color)}.previews-slider__nav-button:hover .previews-slider__nav-button-icon{fill:var(--red-color)}.previews-slider__item{height:490px}@media (max-width:1320px){.previews-slider__item{height:395px}}