.input{width:100%;height:62px;background:var(--extra-light-blue-color);border:1px solid var(--extra-light-blue-color);font-size:16px;padding-left:15px;border-radius:8px;color:var(--black-color);-webkit-transition:none!important;-o-transition:none!important;transition:none!important}.input--small{height:46px}@media (max-width:1320px){.input{height:40px}}.placeholder--not-empty .input{padding-top:14px}.input--search{border-radius:30px;padding-left:63px;font-size:20px;font-weight:600}@media (max-width:1320px){.input--search{font-size:16px;font-weight:400;padding-left:40px}}.input--textarea{min-height:240px;height:auto;padding-top:14px}.product-in-tender .input--textarea{min-height:170px}.placeholder--not-empty .input--textarea{padding-top:30px}.form-check__error--show .input{border-color:var(--red-color);padding-top:14px}.form-check__error--show .input--textarea{padding-top:30px}.input::-webkit-input-placeholder{color:var(--gray-color)}.input:-moz-placeholder,.input::-moz-placeholder{color:var(--gray-color)}.input:-ms-input-placeholder{color:var(--gray-color)}